
.btn-group-container {
  width: 100% !important;
  align-items: center !important;

  /* align-self: flex-end !important; */
  
  border-style: outset; 
  padding: 0;
  background-color: #3f3e44;
  animation-name: loadColor;
  animation-delay: 3s;
  animation-duration: 10s;
  animation-fill-mode: forwards;
  border-color: #3f3e44;
  border-radius: 8px;
  border-style: dotted;
}
.button-navStyle {
  margin: 0 !important;
  align-items: center !important;
}

.out-links {
  width: 100% !important;
  align-items: center !important;
  padding: 0 0;
  font-size: 3vw;
  height: auto;
  margin: 0 !important;
 
}
@keyframes loadColor {
  from {background-color:#3f3e44;}
  to {background-color: white;}
}

.img-links {
  width: 3vw !important;
}

